export const pageSet = {
    data() {
        return {
            listData: [],
            listLoading: false,
            total: 0
        }
    },
    methods: {
        setPage(index) {
            this.listQuery.page = index
            this.getData()
        },
        setPageSize(index) {
            this.listQuery.rows = index
            this.getData()
        }
    },
    watch: {
        listData(to, from) {
            if (to.length === 0 && this.listQuery.page !== 1) {
                if (this.listQuery.page) this.listQuery.page--
            }
        }
    }
}
