<template>
  <div class="register">
    <div class="login-banner meiggin" style="background:url(images/login_banner_bj.jpg) no-repeat center top;margin:30px auto;">

        <div class="lomain meiggin">
            <div class="wframe">
            <div class="title" style="margin-top:10px;"></div>
                <el-form :model="registerForm" status-icon :rules="rules" ref="registerForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="F_RealName">
                        <el-input v-model="registerForm.F_RealName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="F_Account">
                        <el-input v-model="registerForm.F_Account"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="F_Password">
                        <el-input type="password" v-model="registerForm.F_Password" autocomplete="off"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="" prop="checkPass">
                        <el-input type="password" v-model="registerForm.checkPass" autocomplete="off"></el-input>
                    </el-form-item> -->
                    <el-form-item label="" prop="F_Mobile">
                        <el-input type="telphone" v-model="registerForm.F_Mobile" @change='haveNotinput' :error="errorMsg"></el-input>
                        <el-button class="telCode" @click="sendTelephone" :disabled="isdisabledFn"  :loading="buttonLoding">{{btnStr}}</el-button>
                    </el-form-item>
                    
                    <el-form-item label="" prop="VerificationCode" style="margin-top:10px;">
                        <el-input type="telphone" v-model="registerForm.VerificationCode" ></el-input>
                    </el-form-item>
                    <el-checkbox v-model="checked" style="margin-left:114px;"><a><em></em><em></em></a></el-checkbox>
                    <el-form-item>
                        <el-button type="primary" style="margin-left:0;width:80%" @click="submitForm()" class="button"></el-button>
                    </el-form-item>
                    <div class="text2"><a href="login"></a></div>
                </el-form>
            </div>
        </div>

    </div>
  </div>
</template>
<script>
export default {
    name:'register',
    data() {
      return {
        checked:false,
        isdisabledFn:true,
        buttonLoding:false,
        btnStr:'',
        canClick: true,  //canClick
        totalTime: 60,
        errorMsg:'',
        registerForm: {
          F_RealName:"",
          F_Account:'',
          F_Password:'',
          F_Mobile:"",
          VerificationCode:'',
      
        },
        rules: {
            F_RealName: [{ required: true, message: '', trigger: 'blur' }],
            F_Account: [{ required: true, message: '', trigger: 'blur' }],
            F_Password: [{ required: true, message: '', trigger: 'blur' }],
            F_Mobile: [{ required: true, message: '', trigger: 'blur' }],
            VerificationCode: [{ required: true, message: '', trigger: 'blur' }],
        },
    
      };
    },
    methods: {

      sendTelephone(){
        if (!this.canClick) return   //
        this.canClick = false
        this.btnStr = this.totalTime + 's'
        let clock = window.setInterval(() => {
          this.totalTime--
          this.btnStr = this.totalTime + 's'
          if (this.totalTime < 0) {         //0
              window.clearInterval(clock)
              this.btnStr = ''
              this.totalTime = 60
              this.canClick = true   //
              }
        },1000)

        this.$api.registerApi.telephoneCode({
          Tel:this.registerForm.F_Mobile,
          Type:'0'
        }).then(res=>{
          console.log(res)
            // this.errorMsg = res.data.info
        })    
      },
      submitForm() {
  
        this.$api.registerApi.userRegister(
          this.registerForm
        ).then(res=>{

        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      haveNotinput(){
        if(this.registerForm.F_Mobile!==''){
          this.isdisabledFn=false
        }
        if(this.registerForm.F_Mobile==''){
          this.isdisabledFn=true
        }
      }
    },
    created(){

    }
  }
</script>
<style scoped>
@import './../components/style/index.scss';
.demo-ruleForm .el-input{
    width:80% ;
}
.demo-ruleForm .el-checkbox__input{
    float: left;
    margin-top: 14px;
    margin-left: 77px;
}
.demo-ruleForm a{
	display:block;
	float:left;
	font:13px/40px 'microsoft yahei';
	color:#666;
	cursor:pointer;
}
.demo-ruleForm a em{
	color:#09C;
}
.demo-ruleForm a{
    display: inline;
    float:none;
}
.register .login-banner{
    height: 660px;
}
.register .login-banner .lomain .wframe{
  height: 600px;
  width: 500px;
}
.register .telCode{
  /* margin-left:100px; */
  margin-top:10px;
}
</style>
