<template>
  <div class="login">
    <div class="login-banner meiggin" style="background:url(./../../components/style/img/login.jpg) no-repeat center top;margin:30px auto;">

        <div class="lomain meiggin">
            <div class="wframe" style="width:430px;">
            <div class="title" style="margin-top:10px;"></div>
                <el-form :model="registerForm" status-icon :rules="rules" ref="registerForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="Account">
                        <el-input v-model="registerForm.Account" style="width: 256px;"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="Password">
                        <el-input type="password" v-model="registerForm.Password" autocomplete="off" style="width: 256px;"></el-input>
                    </el-form-item>
                        <el-button type="primary" style="margin-left:100px;width: 256px;" @click="submitForm()" class="button"></el-button>
                    </el-form-item>
                    <div class="text2"><router-link to="/register"></router-link></div>
                    <div class="text2" style="margin:0;"><el-button type="text" @click="dialogPassword = true"></el-button></div>
                </el-form>
                <el-dialog title="" :visible.sync="dialogPassword" width='500px'>
                  <el-form :model="Passwordform">
                      <el-form-item label="">
                          <el-input v-model="Passwordform.Mobile" style="width:48%;"></el-input>
                          <el-button style="margin-left:30px;" class="telCode" @click="sendTelephone"   :loading="buttonLoding">{{btnStr}}</el-button>
                      </el-form-item>
                      <el-form-item label="">
                          <el-input v-model="Passwordform.VerificationCode"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                          <el-input v-model="Passwordform.NewPassword"></el-input>
                      </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                      <el-button @click="dialogPassword = false"> </el-button>
                      <el-button type="primary" @click="dialogPasswordFunction"> </el-button>
                  </div>
                </el-dialog>  
            </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
    inject:['reload'],
    name:'login',
    data() {

      return {
        checked:false,
        dialogPassword:false,
        buttonLoding:false,
        btnStr:'',
        totalTime: 60,
        registerForm: {
          Account:'',
          Password:'',
        },
        rules: {
            Account: [{ required: true, message: '', trigger: 'blur' }],
            Password: [{ required: true, message: '', trigger: 'blur' }]
        },
        Passwordform:{
            Mobile:'',
            NewPassword:'',
            VerificationCode:""
        },
      };
    },

    methods: {
      submitForm() {
        this.$api.registerApi.userLogin(
          {
            LoginMark:'1234567890',
            data:JSON.stringify(this.registerForm)
          }
        ).then(res=>{
              //
              if(res.data.code=='200'){
                  let data = res.data.data.Token;
                  let RealName = res.data.data.RealName;
                  window.localStorage["token"] = data;
                  window.localStorage["RealName"] = JSON.stringify(RealName);
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  // //
                  this.reload()
                  this.$router.push('home');
              }else{
                this.$message.error(res.data.info);
              }
              
        }).catch(error => {
          console.log("");
        })
      },
      dialogPasswordFunction(){
          let formObj ={}
          formObj.VerificationCode = this.Passwordform.VerificationCode
          formObj.NewPassword = this.Passwordform.NewPassword
          this.$api.registerApi.resetPassword(
          {
            LoginMark:'1234567890',
            data:JSON.stringify(formObj)
          }
        ).then(res=>{
              //
              if(res.data.code=='200'){
                  
              }else{
                this.$message.error(res.data.info);
              }
              
        }).catch(error => {
          console.log("");
        })
      },
      sendTelephone(){
            this.$api.registerApi.telephoneCode({
                Tel:this.Passwordform.Mobile,
                Type:'1'
            }).then(res=>{
                if(res){
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                    });
                }
            }).catch(error=>{
              this.$message.error('')
            })
          if (!this.canClick) return   //
          this.canClick = false
          this.btnStr = this.totalTime + 's'
          let clock = window.setInterval(() => {
          this.totalTime--
          this.btnStr = this.totalTime + 's'
          if (this.totalTime < 0) {         //0
              window.clearInterval(clock)
              this.btnStr = ''
              this.totalTime = 60
              this.canClick = true   //
              }
          },1000)
      },
    }
  }
</script>
<style scoped>
@import './../components/style/index.scss';
.demo-ruleForm .el-input{
    width:80% ;
}
.demo-ruleForm .el-checkbox__input{
    float: left;
    margin-top: 14px;
    margin-left: 77px;
}
.demo-ruleForm a{
	display:block;
	float:left;
	font:13px/40px 'microsoft yahei';
	color:#666;
	cursor:pointer;
}
.demo-ruleForm a em{
	color:#09C;
}
.demo-ruleForm a{
    display: inline;
    float:none;
}
.login .login-banner{
  height:500px;
}
.login .login-banner .lomain .wframe{
  height: 320px;
}
</style>
