
<template>
<div class="mycarport">
    <el-table :data="listData" size="small" border  style="margin-top:20px;width:100%;"
    :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column align="center" prop="OrderCode" label="" sortable ></el-table-column>
        <el-table-column align="center" prop="ParkingCode" label="" sortable></el-table-column>
        <el-table-column align="center" prop="BuildingName" label="" sortable></el-table-column>
        <el-table-column align="center" prop="Price" label="" sortable></el-table-column>
        <el-table-column align="center" prop="FixedRate" label="" sortable></el-table-column>
        <el-table-column align="center" prop="EnsureMark" label="" sortable></el-table-column>
        <el-table-column align="center" prop="RepoTerm" label="" sortable></el-table-column>
        <el-table-column align="center" label="" width="450" fixed="right">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="carportEdit(scope)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
            </template>
        </el-table-column>
    </el-table>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'MyCarport',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            listData: [],
        };
    },
    methods: {
        getData(){
            let objQuery = {}
            objQuery.ParkingCode=''
            objQuery.state='0,1,2,3,4,5,6'
            this.$api.userInfoApi.ParkingInfo(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify(objQuery),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('')
                }
            }).catch(error=>{
                this.$message.error('')
                this.loading = false
            })
        },
        
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        },
        carportEdit(row){
            console.log(row.row.id)
             this.$router.push({
                path: '/home',
                query: {
                    id: row.row.id
                }
            })
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.mycarport .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>