
<template>
<div class="orderhistory">
    <el-input placeholder="" v-model="OrderId" class="input-with-select" style="width:40%;">
    <el-button slot="append" icon="el-icon-search" @click="orderSearch"></el-button>
    </el-input>
    <el-table :data="listData" size="small" border  style="margin-top:20px"
    :default-sort = "{prop: 'date', order: 'descending'}" v-loading="loading">
        <el-table-column align="center" prop="OrderCode" label="" sortable></el-table-column>
        <el-table-column align="center" prop="Price" label="" sortable></el-table-column>
        <el-table-column align="center" prop="PayType" label="" sortable></el-table-column>
        <el-table-column align="center" prop="OrderState" label="" sortable></el-table-column>
        <el-table-column align="center" prop="CreateDate" label="" sortable></el-table-column>
    </el-table>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'OrderHistory',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            listData: [],
            loading: true,
            OrderId:''
        };
    },
    methods: {
        getData(){
            // let objQuery = {}
            // objQuery.OrderId=''
            // objQuery.OrderState="0"
            this.loading = true
            this.$api.userInfoApi.MyOrder(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify({OrderId:this.OrderId,OrderState:"3,-1,-2,-3,4,5"}),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('')
                }
            }).catch(error=>{
                this.$message.error('')
                this.loading = false
            })
        },
        orderSearch(){
            // let objQuery = {}
            // objQuery.OrderId=this.OrderId
            this.getData()
        },
        // orderChange(){
        //     if(this.OrderId=''){
        //         this.getData()
        //     }
        // },
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.orderhistory .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>