<template>
 <bm-overlay
    ref="customOverlay"
    pane="labelPane"
    :class="{sample:true, active}"
    @draw="draw"
    >
    <!-- v-for="item in rows"
    :key="item.BuildingId" -->
    
    <div  @click="handleYuanClick">
        <p style="margin:12px 0 0 0;">{{itemcity}}</p>
        <p style="margin:10px 0 0 0;">{{citymap.card}}</p>
    </div>
</bm-overlay>
</template>

<script>
export default {

  props: ['position', 'active','itemcity'],
  data(){
    return{
      yuanShow:true,
      citymap:{
          District:'',
          card:'55'
      },
      
    }
  },
  watch: {
    position: {
      handler () {
        this.$refs.customOverlay.reload()
      },
      deep: true
    }
  },
  methods: {
     handleYuanClick(){
           
        this.mapZoom = 0;
        this.$nextTick(function () {
            this.mapZoom = 13
        });
    },
    draw ({el, BMap, map}) {
      const {lng, lat} = this.position
      const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat))
      el.style.left = pixel.x - 60 + 'px'
      el.style.top = pixel.y - 20 + 'px'
      if(13<=map.Oa||map.Oa<=10){
          this.yuanShow = false
          this.$nextTick(function () {
              this.yuanShow = false
          });
      }else{
          this.yuanShow = true
          this.$nextTick(function () {
              this.yuanShow = true
          });
      }
    }
  }
}
</script>

<style>
.sample {
    width: 60px;
    height: 60px;
    line-height: 10px;
    border-radius: 50%;
    background: rgba(0,0,0,0.5);
    overflow: hidden;
    box-shadow: 0 0 5px #000;
    color: #fff;
    text-align: center;
    padding: 10px;
    position: absolute;
}
.sample.active {
    background: rgba(0,0,0,0.75);
    color: #fff;
}
</style>