
<template>
<div class="earnings">
    
    <el-table :data="listData" size="small" border  style="margin-top:20px" v-loading="loading" 
    :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column align="center" prop="ProfitTime" label="" sortable></el-table-column>
        <el-table-column align="center" prop="ProfitRate" label="" sortable></el-table-column>

    </el-table>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'MyEarnings',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            listData: [],
            loading: true
        };
    },
    methods: {
        getData(){
            this.$api.userInfoApi.MyProfit(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('')
                }
            }).catch(error=>{
                this.$message.error('')
                this.loading = false
            })
        },
        setPage(val) {
            this.listQuery.page = val
            this.getData()
        },
        setSize(val) {
            this.listQuery.rows = val
            this.getData()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.earnings .pagination-container{
    margin-top:20px;
    text-align: center
}
</style>