
<template>
    <div class="serinfo">
        <div class="elite_users_info_head clear">
            <img src="./../../../public/img/head.png" alt="" style="float:left;margin-left:15px;">
            <div class=" elite_users_info_head_text" style="float:left">
                <h4>{{RealName}}</h4>
                <span></span>
            </div>
        </div>
        <el-form ref="form" :model="form" label-width="100px" style="margin-top:40px;padding-bottom:20px;border-bottom: 1px solid #dadada; ">
            <el-form-item label="">
                <el-upload
                    class="avatar-uploader"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.RealName"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.Account" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.Mobile"></el-input>
                <!-- <el-button type="success" :disabled='disabled' style="margin-left:30px;"></el-button> -->
                <el-button style="margin-left:30px;" class="telCode" @click="sendTelephone"   :loading="buttonLoding">{{btnStr}}</el-button>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="form.VerificationCode" placeholder=""></el-input>
            </el-form-item>
            <!-- <el-form-item label="">
                <el-input v-model="form.name"></el-input>
            </el-form-item> -->
            <el-button type="info" @click="editUserInfo" style="margin-left:100px"></el-button>
            <el-button type="info" @click="dialogPassword = true" style="margin-left:10px"></el-button>
            <el-dialog title="" :visible.sync="dialogPassword">
            <el-form :model="Passwordform">
                <el-form-item label="" label-width="120px">
                    <el-input v-model="Passwordform.Password"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="Passwordform.NewPassword"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogPassword = false"> </el-button>
                <el-button type="primary" @click="dialogPasswordFunction"> </el-button>
            </div>
            </el-dialog>
        </el-form>
        <el-form ref="form" :model="form" label-width="100px" style="margin-top:20px;">
            <el-form-item label="" style="margin-top:10px">
                <el-table
                    :data="tableData"
                    v-loading="loading"
                    style="width: 100%">
                    <el-table-column
                    prop="OpeningbankName"
                    label=""
                    width="150">
                    </el-table-column>
                    <el-table-column
                    prop="CardNo"
                    label=""
                    >
                    </el-table-column>
                    <el-table-column
                    prop="OpeningbankNo"
                    label=""
                    width="120">
                    </el-table-column>
                    <el-table-column
                    prop="OpeningbankAddress"
                    label=""
                    width="120">
                    </el-table-column>
                    <el-table-column
                    label=""
                    width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                    </template>
                    </el-table-column>
                </el-table>
                <div style="margin-top: 20px">
                    <el-button @click="dialogBank = true"></el-button>
                </div>
                <el-dialog
                title=""
                :visible.sync="dialogBank"
                width="500px"
                :before-close="handleClose"
                class="dialog_wrap_bank">
                <el-form :model="addBankform"  label-width="100px">
                    <el-form-item label="" style="margin-bottom: 22px;">
                        <el-input v-model="addBankform.CardNo"></el-input>
                    </el-form-item>
                    <el-form-item label="" style="margin-bottom: 22px;">
                        <el-input v-model="addBankform.OpeningbankNo"></el-input>
                    </el-form-item>
                    <el-form-item label="" style="margin-bottom: 22px;">
                        <el-input v-model="addBankform.OpeningbankName"></el-input>
                    </el-form-item>
                    <el-form-item label="" style="margin-bottom: 22px;">
                        <el-input v-model="addBankform.OpeningbankAddress"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogBank = false"> </el-button>
                    <el-button type="primary" @click="adddialogBank"> </el-button>
                </span>
                </el-dialog>
            </el-form-item>
        </el-form>
    </div>
</template>


<script>
  export default {
    name:'MyserInfo',
    inject:['reload','reloadClass'],
    data() {
        return {
            imageUrl: '',
            dialogBank: false,
            buttonLoding:false,
            btnStr:'',
            canClick: true,  
            totalTime: 60,
            oldTelephone:'',
            dialogPassword:false,
            loading: true,
            RealName:'',
            form: {
                VerificationCode: '',
                RealName:'',
                Account:'',
                Mobile:"",
            },
            Passwordform:{
                Password:'',
                NewPassword:'',
            },
            addBankform:{
                CardNo:'',
                OpeningbankNo:'',
                OpeningbankName:'',
                OpeningbankAddress:'',
            },
            tableData: []
        };
    },
    methods: {
        getData(){
           this.$api.userInfoApi.getUserInfo(
                {
                LoginMark:'1234567890',
                Token:localStorage.token
                // localStorage.getItem('token')
                }
            ).then(res=>{
                if(res.data.data){
                    this.form = res.data.data
                    this.oldTelephone = res.data.data.Mobile
                    this.tableData = res.data.data.CardList
                    this.loading = false
                    this.RealName = this.form.RealName
                }
            }).catch(error=>{
                console.log('')
                this.loading = false
            })
        },
        sendTelephone(){
            if(this.oldTelephone!==this.form.Mobile){
                this.$api.registerApi.telephoneCode({
                    Tel:this.form.Mobile,
                    Type:'2'
                }).then(res=>{
                    if(res){
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                    }
                })
            }else{
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
                return
            }
            if (!this.canClick) return   //
            this.canClick = false
            this.btnStr = this.totalTime + 's'
            let clock = window.setInterval(() => {
            this.totalTime--
            this.btnStr = this.totalTime + 's'
            if (this.totalTime < 0) {         //0
                window.clearInterval(clock)
                this.btnStr = ''
                this.totalTime = 60
                this.canClick = true   //
                }
            },1000)
        },
        editUserInfo() {
            if(this.oldTelephone !== this.form.Mobile){
                let formObj = {}
                formObj.Mobile = this.form.Mobile
                formObj.VerificationCode = this.form.VerificationCode
                formObj.RealName = this.form.RealName
                this.$api.userInfoApi.UpdateUser(
                    {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    data:JSON.stringify(formObj)
                    }
                ).then(res=>{
                    if(res){
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success' 
                        });
                        window.localStorage["RealName"] = JSON.stringify(formObj.RealName);
                        this.reloadClass()
                        return
                    }
                    
                }).catch(error=>{
                    console.log('')
                })
            }else{
                let formObj = {}
                formObj.RealName = this.form.RealName
                this.$api.userInfoApi.UpdateUser(
                    {
                        LoginMark:'1234567890',
                        Token:localStorage.token,
                        data:JSON.stringify(formObj)
                    }
                ).then(res=>{
                    if(res){
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success' 
                        });
                    window.localStorage["RealName"] = JSON.stringify(formObj.RealName);
                        this.reload()
                        return
                    }
                    
                }).catch(error=>{
                    console.log('')
                })
            }
            
        },
        dialogPasswordFunction(){
            if(this.Passwordform.Password == this.Passwordform.NewPassword){
                this.$message.error('');
            }
            if(this.Passwordform.Password !== this.Passwordform.NewPassword){
                this.$api.registerApi.editPassword(
                    {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    data:JSON.stringify(this.Passwordform)
                    }
                ).then(res=>{
                    if(res.data.code=='200'){
                        console.log(res)
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success' 
                        });
                        this.$api.registerApi.userLoginOut(
                        {
                            LoginMark:'1234567890',
                            Token:localStorage.getItem('token')
                        }
                        
                        ).then(res=>{
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success' 
                            });
                        })
                        localStorage.clear()
                        this.reload()
                        this.$router.push('login')
                    }
                    if(res.data.code=='400'){
                        this.$message.error('');
                    }
                    this.dialogPassword = false
                    
                }).catch(error=>{
                    console.log('')
                })
            }
        },
        onSubmit(){

        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        handleClick(row) {
            this.loading = true
            let rowObj = {}
            rowObj.CardNo = row.CardNo
            this.$api.userInfoApi.UntyingCard(
                {
                LoginMark:'1234567890',
                Token:localStorage.token,
                data:JSON.stringify(rowObj)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    console.log(res)
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success' 
                    });
                    this.getData()
                    this.loading = false
                }
                if(res.data.code=='400'){
                    
                }
                this.dialogBank = false
                
            }).catch(error=>{
                console.log('')
            })
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg'||'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
            this.$message.error(' JPG !');
            }
            if (!isLt2M) {
            this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        adddialogBank(){
            this.loading = true
            this.$api.userInfoApi.bindCard(
                {
                LoginMark:'1234567890',
                Token:localStorage.token,
                data:JSON.stringify(this.addBankform)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    console.log(res)
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success' 
                    });
                    this.getData()
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.$message.error('')
                }
                this.dialogBank = false
                
            }).catch(error=>{
                console.log('')
            })
        }
    },
    mounted(){
        this.getData()
    }
  }
</script>
<style scoped>
  .serinfo .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .serinfo .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .serinfo .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    border:1px solid #d9d9d9;
  }
  .serinfo .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .serinfo .dialog_wrap_bank .el-input{
      width:88%;
  }
  .serinfo .el-input{
      width:50%;
  }
  .elite_users_info_head {
      padding-bottom: 30px;
      border-bottom: 1px solid #dadada; }
      .elite_users_info_head img {
        width: 105px; }
        .elite_users_info_head .elite_users_info_head_text {
        margin: 0 0 0 35px; }
        .elite_users_info_head .elite_users_info_head_text h4 {
          font-size: 21px;
          color: #333333;
          font-weight: 700;
          margin-bottom: 20px; }
          .elite_users_info_head .elite_users_info_head_text span {
          color: #666666;
          font-size: 16px;
          cursor: pointer; }
.clear{zoom:1;}
.clear:before{content:"";display:table;}
.clear:after{content:"";clear:both;display:block;}
</style>